
































import Vue from 'vue'
import Component from 'vue-class-component'
import Raven from 'raven-js'
import InvoiceInfo from './InvoiceInfo.vue'
import * as apiService from '../service/apiService'
import { Message } from 'element-ui'

@Component({
  components: {
    InvoiceInfo
  }
})
export default class Invoice extends Vue {
  show: Boolean = false
  userInfo = null
  invoiceInfoVisible: Boolean = false
  currentPage: number = 1
  invoiceList: any[] = []
  currentPageData: any[] = []
  loadingInfo: Boolean = false // 加载订单详情
  currentInvoiceProgressList: null // 当前订单详情
  pageSize: number = 5 // 每页数据条数

  currentChange (pageNum: number): void {
    const start: number = (pageNum - 1) * this.pageSize
    const end = this.invoiceList.length < start + this.pageSize ? this.invoiceList.length : start + this.pageSize
    this.currentPageData = this.invoiceList.slice(start, end)
  }
  clickItem (item): void {
    apiService.getInvoiceProcessList(item.id)
      .then(data => {
        if (data.length === 0) {
          Message({
            message: '订单详情数据错误',
            type: 'error',
            showClose: true
          })
        } else {
          this.currentInvoiceProgressList = data
          if (!this.show) {
            this.show = true
            setTimeout(() => {
              this.invoiceInfoVisible = true
            }, 200)
          } else {
            this.invoiceInfoVisible = true
          }
        }
      })
      .catch(err => {
        Raven.captureException(err)
        Message({
          message: '获取订单信息失败',
          type: 'error',
          showClose: true
        })
      })
  }
  closeDialog (): void {
    this.invoiceInfoVisible = false
  }
  created (): void {
    // 计算每页显示数据条数
    this.pageSize = Math.floor((window.document.documentElement.clientHeight * 0.97 - 119) / 101)
    this.userInfo = apiService.getLocalUserInfo()
    apiService.getInvoiceList(this.userInfo.id, 1, 100)
      .then(res => {
        this.invoiceList = res.data
        const end = this.invoiceList.length >= this.pageSize ? this.pageSize : this.invoiceList.length - 1
        this.currentPageData = this.invoiceList.slice(0, end)
      })
      .catch(err => {
        Raven.captureException(err)
        Message({
          message: '获取订单列表失败',
          type: 'error',
          showClose: true
        })
      })
  }
}



